    videoStopped (e) {
      //  //获取视频标签
      //  //停止播放
      e.target.pause();
    },

    fn (a, i) {
      let routeData = this.$router.resolve({ name: "Xiazai", query: { id: a, i: i } });
      window.open(routeData.href, '_blank');
    },
    // 音频文件
    play (i) {
      if (this.doms[i].paused) {
        if (this.playz != null) {
          this.doms[this.playz].pause()
        }
        this.doms[i].play()
        this.playz = i
      } else {
        this.doms[i].pause()
        this.playz = null
      }
      this.music[i].isPlay = !this.doms[i].paused
      // this.$nextTick(() => {
      //   document.getElementById('play').blur()
      // })
    },
    changeTime (i) {
      this.doms[i].currentTime = this.music[i].currentTime
    },
    changeVolume (i, v) {
      this.music[i].volume += v
      if (this.music[i].volume > 100) {
        this.music[i].volume = 100
      }
      if (this.music[i].volume < 0) {
        this.music[i].volume = 0
      }
      this.doms[i].volume = this.music[i].volume / 100
    },
    formatTime (time) {
      let it = parseInt(time)
      let m = parseInt(it / 60)
      let s = parseInt(it % 60)
      return (m < 10 ? "0" : "") + parseInt(it / 60) + ":" + (s < 10 ? "0" : "") + parseInt(it % 60)
    }
  },
  mounted () {

    // 音频文件
    this.$nextTick(() => {
      // setInterval(this.listenMusic, 1000)
      setInterval(() => {
        for (var i = 0; this.music.length > i; i++) {
          if (!this.doms[i]) {
            return
          }
          if (this.doms[i].readyState) {
            this.music[i].maxTime = this.doms[i].duration
          }
          this.music[i].isPlay = !this.doms[i].paused
          this.music[i].currentTime = this.doms[i].currentTime
        }
      }, 1000)
    })



  },
  setup () {

    const restaurants = ref([]);
    const querySearch = (queryString, cb) => {
      var results = queryString
        ? restaurants.value.filter(createFilter(queryString))
        : restaurants.value;
      // 调用 callback 返回建议列表的数据
      cb(results);
    };
    const createFilter = (queryString) => {
      return (restaurant) => {
        return (
          restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) ===
          0
        );
      };
    };
    const loadAll = () => {
      return [
        { value: "建党100年宣传" },
        { value: "UI设计" },
        { value: "网页设计" },
        { value: "单页海报" },
        { value: "DM单" },
        { value: "吊旗" },
        { value: "党建宣传" },
        { value: "秋分" },
        { value: "618海报" },
        { value: "特效粒子视频" },
        { value: "店庆海报" },
        { value: "商务PPT模板" },
        { value: "企业画册" },
        { value: "电商宣传" },

      ];
    };
    const handleSelect = (item) => {
      console.log(item);
    };
    onMounted(() => {
      restaurants.value = loadAll();
    });
    return {
      restaurants,
      state1: ref(''),
      state2: ref(''),
      querySearch,
      createFilter,
      loadAll,
      handleSelect,



    };
  },
}
</script>
